import React, { Component } from 'react';
import { BrowserRouter as Router, Link } from 'react-router-dom';
import Menu from './Components/Menu';
import './App.css';
import LoginModal from './Components/LoginModal';
import SignModal from './Components/SignModal';

class App extends Component {
  render() {
    return (
      <Router>
        <div className="nav-bar">
          <nav class="navbar navbar-toggleable-md navbar-inverse bg-inverse fixed-top">
            <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="/navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
              <span class="navbar-toggler-icon"></span>
            </button>
            <Link class="navbar-brand" to="/">Navbar</Link>

            <div class="collapse navbar-collapse" id="navbarsExampleDefault">
              <ul class="navbar-nav mr-auto">
                <li class="nav-item active">
                  <Link class="nav-link" to="/">Browse <span class="sr-only">(current)</span></Link>
                </li>
                <li class="nav-item">
                  <Link class="nav-link" to="/">Get DeskTop</Link>
                </li>
                <li class="nav-item">
                  <Link class="nav-link" to="/">Try Prime</Link>
                </li>
                <li class="nav-item">
                  <Link class="nav-link" to="/">Store</Link>
                </li>
                <Menu />
                <input type="text" className="search" placeholder="Search.." name="search"></input>
              </ul>
            </div>
            <div className="navbar-nav ml-auto">
              <div className="dropdown">
                <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  Get Bits
                </button>
                <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                  <Link className="dropdown-item" to="/">Action</Link>
                  <Link className="dropdown-item" to="/">Another action</Link>
                  <Link className="dropdown-item" to="/">Something else here</Link>
                </div>
              </div>
              <LoginModal />
              <SignModal />
            </div>
          </nav>
        </div>
      </Router>
    )
  }
}
export default App;


